import angular from 'angular';
import ngResource from 'angular-resource';
import { InterestListService } from './services/interest-list.service';
import { MissionService } from './services/mission.service';
import { TeamService } from './services/team.service';
import { TitleService } from './services/title.service';
import { RouteLoadingIndicator } from './directives/route-loading-indicator.directive';
import { NgEnter } from './directives/ng-enter.directive';
import { MagnificPopup } from './directives/magnific-popup.directive';
import { ThumbnailScroller } from './directives/mThumbnailScroller.directive';

export const CoreModule = angular
  .module('core', [
    ngResource
  ])
  .directive('magnific', () => new MagnificPopup())
  .directive('mThumbnailScroller', () => new ThumbnailScroller())
  .directive('ngEnter', () => new NgEnter())
  .directive('routeLoadingIndicator', /* @ngInject */ ($rootScope) => new RouteLoadingIndicator($rootScope))
  .service('InterestList', InterestListService)
  .service('Mission', MissionService)
  .service('Team', TeamService)
  .service('titleService', TitleService)
  .name;