'use strict';

export class TeamService {
	constructor($resource) {
		"ngInject";
		this.$resource = $resource;
	}

	getTeams(year, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/teams', {}, {
			query: { method: 'GET', params: { year: 'year' }, isArray: true, headers: { 'Accept': 'application/json' }, withCredentials: true }
		}).query({ year: !year ? 2021 : year }, callback);
	}

	getTeam(id, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/teams/:id', {}, {
			get: { method: 'GET', params: {}, isArray: false, headers: { 'Accept': 'application/json' } }
		}).get({ id: id }, callback);
	}

	getAvailableYears(callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/teams/availableyears', {}, {
			get: { method: 'GET', params: {}, isArray: true, headers: { 'Accept': 'application/json' }, cache: true }
		}).get({}, callback);
	}
	addPersonToInterestList(teamId, personId, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/teams/:id/interestlist/person/:personId/add',
			{ id: teamId, personId: personId }, {
				post: { method: 'POST', params: {}, headers: { 'Accept': 'application/json' }, withCredentials: true }
			}).post({}, callback);
	}

	removePersonFromInterestList(teamId, personId, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/teams/:id/interestlist/person/:personId/remove',
			{ id: teamId, personId: personId }, {
				post: { method: 'POST', params: {}, headers: { 'Accept': 'application/json' }, withCredentials: true }
			}).post({}, callback);
	}
}
