'use strict';

import angular from 'angular';

// from https://github.com/ngbp/ngbp/blob/e84c2469b3eb696b04c5be8601dda46fa48393e2/src/common/titleService/titleService.js
export class TitleService {
  constructor($document) {
    "ngInject";
    this.$document = $document;
  }

  setSuffix(s) {
    this.suffix = s;
  }

  getSuffix() {
    return this.suffix;
  }

  setTitle(t) {
    let title = t;

    if (angular.isDefined(this.suffix)) {
      title += this.suffix;
    }

    this.$document.prop( 'title', title );
  }

  getTitle() {
    return this.$document.prop( 'title' );
  }
}
