'use strict';

export class OrganizationsController {
	constructor($scope, organizations, regions, paginationService) {
		"ngInject";

		this.$scope = $scope;
		this.organizations = organizations;

		$scope.paginationService = paginationService;
		$scope.filteredOrganizations = organizations;
		$scope.regions = regions;
		$scope.selectedRegion = null;
		$scope.searchStr = '';

		this.calculateRegionCounts();

		$scope.regionChanged = () => {
			this.filterOrganizations();
		};

		$scope.$watch('searchStr', (newValue, oldValue) => {
			if (newValue !== oldValue) {
				$scope.searchBoxStr = $scope.searchStr;
				this.filterOrganizations();
			}
		});
	}

	filterOrganizations() {
		this.$scope.filteredOrganizations = this.getFilteredOrganizations();

		this.calculateRegionCounts();
	}

	getFilteredOrganizations(excludeRegionFilter) {
		var filteredItems = [];

		for (var i = 0; i < this.organizations.length; i++) {
			var org = this.organizations[i];
			var matched = true;

			if (this.$scope.searchStr) {
				matched = org.Name && org.Name.toLowerCase().indexOf(this.$scope.searchStr.toLowerCase()) >= 0
					|| org.CountryName && org.CountryName.toLowerCase().indexOf(this.$scope.searchStr.toLowerCase()) >= 0;
			}
			if (this.$scope.selectedRegion && !excludeRegionFilter) {
				matched = matched && org.Region === this.$scope.selectedRegion.Key;
			}

			if (matched) {
				filteredItems.push(org);
			}
		}

		return filteredItems;
	}

	calculateRegionCounts() {
		for (var i = 0; i < this.$scope.regions.length; i++) {
			this.$scope.regions[i].Count = 0;
		}

		var mostlyFilteredOrgs = this.getFilteredOrganizations(true);

		for (i = 0; i < mostlyFilteredOrgs.length; i++) {
			for (var j = 0; j < this.$scope.regions.length; j++) {
				if (this.$scope.regions[j].Key === mostlyFilteredOrgs[i].Region) {
					this.$scope.regions[j].Count++;
					break;
				}
			}
		}
	}
}
