'use strict';

import fs from 'fs';
import moment from 'moment';

export class TeamsController {
	constructor($scope, $location, $routeParams, $uibModal, Team, teams, availableYears, interestLists) {
		"ngInject";

		this.$scope = $scope;
		this.$location = $location;
		this.$routeParams = $routeParams;
		this.$uibModal = $uibModal;
		this.Team = Team;

		$scope.teams = teams;
		$scope.availableYears = availableYears;
		$scope.interestLists = interestLists;

		// constants
		$scope.oneWeekFromNow = moment().add(7, 'days');

		$scope.selectedYear = this.$routeParams.year || availableYears[0];

		// convert ASP.Net date strings to momentjs objects
		for (var i = 0; i < $scope.teams.length; i++) {
			if ($scope.teams[i].RegistrationStart) {
				$scope.teams[i].RegistrationStart = moment($scope.teams[i].RegistrationStart);
			}
		}

		$scope.$watch('selectedYear', (newValue, oldValue) => {
			if (newValue !== oldValue) {
				this.$location.search({ year: newValue });
			}
		});

		$scope.isInterestedIn = (team) => {
			if (!team.InterestList) {
				return false;
			}

			for (var i = 0; i < this.$scope.interestLists.length; i++) {
				if (this.$scope.interestLists[i].id === team.InterestList.id) {
					return true;
				}
			}

			return false;
		};

		$scope.addToInterestList = (team) => {
			if (RVC.GlobalProject.user) {
				this.Team.addPersonToInterestList(team.ID, RVC.GlobalProject.user.id, (interestList) => {
					team.InterestList = interestList;

					var found = false;
					for (var i = 0; i < this.$scope.interestLists.length; i++) {
						if (this.$scope.interestLists[i].id === team.InterestList.id) {
							found = true;
							break;
						}
					}
					if (!found) {
						this.$scope.interestLists.push(interestList);
					}

					this.$uibModal.open({
						template: fs.readFileSync('src/views/modals/interested-in-team.html', 'utf8'),
						controller: /* @ngInject */ ($scope) => {
							$scope.team = team;
						}
					});
				});
			} else {
				this.$uibModal.open({
					template: fs.readFileSync('src/views/modals/login-required.html', 'utf8')
				});
			}
		};

		$scope.removeFromInterestList = (team) => {
			Team.removePersonFromInterestList(team.ID, RVC.GlobalProject.user.id, () => {
				team.InterestList = null;
			});
		};
	}
}
