'use strict';

export class MissionariesController {
	constructor($scope, $window, $timeout, $routeParams, Mission, missionaries, regions, paginationService) {
		"ngInject";

		this.$scope = $scope;
		this.$window = $window;
		this.$timeout = $timeout;
		this.$routeParams = $routeParams;
		this.Mission = Mission;
		this.missionaries = missionaries;

		$scope.paginationService = paginationService;
		$scope.filteredMissionaries = missionaries;
		$scope.regions = regions;
		$scope.selectedRegion = null;
		$scope.searchStr = '';

		// add Unlisted Missionaries to list of regions
		$scope.regions.push({ Key: 'unlisted', Description: 'Unlisted Missionaries' });

		this.calculateRegionCounts();

		$scope.regionChanged = () => {
			if (this.$scope.selectedRegion && this.$scope.selectedRegion.Key === 'unlisted') {
				this.$window.open(RVC.GlobalProject.unlistedMissionariesUrl, '_blank');

				// revert back to previous region -- has to be done in a separate operation to make it take effect
				this.$timeout(() => {
					this.$scope.selectedRegion = this.$scope.prevSelectedRegion;
				});

				return;
			}

			this.filterMissionaries();

			this.$scope.prevSelectedRegion = this.$scope.selectedRegion;
		};

		$scope.$watch('searchStr', (newValue, oldValue) => {
			if (newValue !== oldValue) {
				this.$scope.searchBoxStr = this.$scope.searchStr;
				this.filterMissionaries();
			}
		});
	}

	filterMissionaries() {
		this.$scope.filteredMissionaries = this.getFilteredMissionaries();

		this.calculateRegionCounts();
	}

	getFilteredMissionaries(excludeRegionFilter) {
		var filteredItems = [];

		for (var i = 0; i < this.missionaries.length; i++) {
			var missionary = this.missionaries[i];
			var matched = true;

			if (this.$scope.searchStr) {
				matched = missionary.Name && missionary.Name.toLowerCase().indexOf(this.$scope.searchStr.toLowerCase()) >= 0
					|| missionary.CountryName && missionary.CountryName.toLowerCase().indexOf(this.$scope.searchStr.toLowerCase()) >= 0;
			}
			if (this.$scope.selectedRegion && !excludeRegionFilter) {
				matched = matched && missionary.Region === this.$scope.selectedRegion.Key;
			}

			if (matched) {
				filteredItems.push(missionary);
			}
		}

		return filteredItems;
	}

	calculateRegionCounts() {
		for (var i = 0; i < this.$scope.regions.length; i++) {
			this.$scope.regions[i].Count = 0;
		}

		var mostlyFilteredMissionaries = this.getFilteredMissionaries(true);

		for (i = 0; i < mostlyFilteredMissionaries.length; i++) {
			for (var j = 0; j < this.$scope.regions.length; j++) {
				if (this.$scope.regions[j].Key === mostlyFilteredMissionaries[i].Region) {
					this.$scope.regions[j].Count++;
					break;
				}
			}
		}
	}

}
