'use strict';

export class MissionService {
	constructor($resource) {
		"ngInject";
		this.$resource = $resource;
	}

	getMissionaries(searchStr, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/missionaries', {}, {
			query: { method: 'GET', params: {}, isArray: true, headers: { 'Accept': 'application/json' } }
		}).query({ searchStr: searchStr }, callback);
	}

	getMissionary(id, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/missionaries/:id', {}, {
			get: { method: 'GET', params: {}, isArray: false, headers: { 'Accept': 'application/json' } }
		}).get({ id: id }, callback);
	}

	getMissionOrganizations(searchStr, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/missionorganizations', {}, {
			query: { method: 'GET', params: {}, isArray: true, headers: { 'Accept': 'application/json' } }
		}).query({ searchStr: searchStr }, callback);
	}

	getMissionOrganization(id, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/missionorganizations/:id', {}, {
			get: { method: 'GET', params: {}, isArray: false, headers: { 'Accept': 'application/json' } }
		}).get({ id: id }, callback);
	}

	getRegions(callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/regions', {}, {
			get: { method: 'GET', params: {}, isArray: true, headers: { 'Accept': 'application/json' }, cache: true }
		}).get({}, callback);
	}
}
