'use strict';

export class InterestListService {
	constructor($resource) {
		"ngInject";
		this.$resource = $resource;
	}

	find(type, callback) {
		return this.$resource('https://api.rivervalley.org/globalproject/interestlists', {}, {
			query: { method: 'GET', params: {}, isArray: true, headers: { 'Accept': 'application/json' }, withCredentials: true }
		}).query({ type: type, personId: RVC.GlobalProject.user.id }, callback);
	}
}
